import { useTranslation } from 'react-i18next';
import { Avatar } from '@/components/Avatar/Avatar';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { useAuth } from '@/features/auth/contexts';
import { UserUtil } from '@/utils/user';
import { type AvatarSizeType } from '@/components/Avatar/Avatar/types';
import { Container, TextSection, CreateButton } from './styles';

type CreateSectionProps = {
  onClick: () => void;
  isCreatable: boolean;
  size?: AvatarSizeType;
  noumEditor2?: boolean;
  isPostView?: boolean;
};
export const CreateSection = (props: CreateSectionProps) => {
  const { user } = useAuth();
  const { onClick, size, noumEditor2, isPostView } = props;
  const { t } = useTranslation();

  return (
    <Container noumEditor2={noumEditor2} isPostView={isPostView}>
      <Avatar
        url={UserUtil.getProfilePicture(user) ?? ''}
        size={size || 'XL'}
      />
      <TextSection onClick={onClick} noumEditor2={noumEditor2}>
        <TSpan font="body-l" colorToken="--text-input-neutral-default">
          {t('noumena.create_post_placeholder')}
        </TSpan>
      </TextSection>
      {!noumEditor2 && (
        <CreateButton onClick={onClick} isCreatable={props.isCreatable}>
          <Icon
            color={
              props.isCreatable
                ? '--icon-button-brand-secondary-default'
                : '--icon-button-brand-primary-disabled'
            }
            name="send_m"
            size={24}
          />
        </CreateButton>
      )}
    </Container>
  );
};
